.page {
  --border-color: #000;
  --minute-hand: #0700ff;
  --second-hand: #2c8000;
  --center-color: #f6a603;
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  padding: 0;
  display: flex;
}

.stopwatch {
  border: 1vmin dotted var(--border-color);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 80vmin;
  height: 80vmin;
  margin: 0;
  display: flex;
  position: relative;
}

.stopwatch__center {
  background-color: var(--center-color);
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  display: block;
  position: absolute;
}

.stopwatch__minute-hand {
  background-color: var(--minute-hand);
  transform-origin: bottom;
  width: 3vmin;
  height: 20vmin;
  animation: 3600s steps(60, end) infinite move;
  display: block;
  position: absolute;
  bottom: 50%;
}

.stopwatch__second-hand {
  background-color: var(--second-hand);
  transform-origin: bottom;
  width: 1.5vmin;
  height: 38vmin;
  animation: 60s linear infinite move;
  display: block;
  position: absolute;
  bottom: 50%;
}

.stopwatch--speed-up .stopwatch__minute-hand {
  animation-duration: 600s;
}

.stopwatch--speed-up .stopwatch__second-hand {
  animation-duration: 10s;
}

@keyframes move {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}
